import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Loader from "react-loader-spinner";

// Actions
import { login } from "actions/user";

import PrimaryButton from "../../elements/PrimaryButton";
import Alert from "elements/Alert";

class LoginPage extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      formData: {
        email: "",
        password: "",
      },
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleLoginFormSubmit = this.handleLoginFormSubmit.bind(this);
  }

  handleChange({ target }) {
    let { name, value } = target;

    this.setState((prevState) => ({
      ...prevState,
      formData: { ...prevState.formData, [name]: value },
    }));
  }

  handleLoginFormSubmit(e) {
    e.preventDefault();

    // console.log("login fomr data = ", this.state.formData);

    this.props.login(this.state.formData);
  }

  render() {
    return (
      <div className="w-full h-screen flex flex-col justify-center items-center bg-gray-300">
        <img
          className="w-16"
          src="/assets/images/botsite-logo.svg"
          alt="Botsite Logo"
        />
        <h2 className="text-4xl text-gray-700">Sign in to Botsite</h2>

        <form
          className="w-independent-form bg-gray-300 rounded px-4 py-4 pt-8"
          onSubmit={this.handleLoginFormSubmit}
        >
          {!!this.props.loginError && (
            <Alert type="error" heading={this.props.loginError} />
          )}

          <div className="pb-4">
            <label htmlFor="email" className="text-sm block text-gray-500 pb-2">
              Email
            </label>
            <input
              type="email"
              name="email"
              id="email"
              className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300 "
              placeholder="Johnbull@example.com"
              value={this.state.formData.email}
              onChange={this.handleChange}
            />
          </div>
          <div className="pb-4">
            <label
              htmlFor="password"
              className="text-sm block text-gray-500 pb-2"
            >
              Password
            </label>
            <input
              type="password"
              name="password"
              id="password"
              className="shadow appearance-none border rounded w-full py-4 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-gray-300"
              placeholder="Enter your password"
              value={this.state.formData.password}
              onChange={this.handleChange}
            />
          </div>
          <div>
            {this.props.isLoading ? (
              <PrimaryButton className="w-full bg-primary-1" type="submit">
                <Loader
                  className="flex items-center justify-center"
                  type="ThreeDots"
                  color="#FFFFFF"
                  secondaryColor="#FFFFFF"
                  height={25}
                  width={40}
                  timeout={0}
                />
              </PrimaryButton>
            ) : (
              <PrimaryButton className="w-full bg-primary-2" type="submit">
                Login
              </PrimaryButton>
            )}
          </div>
        </form>
        <div>
          <p className="py-2 pt-6 text-gray-600">
            Don't have an account yet?{" "}
            <Link to="/register" className="text-blue-400">
              Sign Up
            </Link>
          </p>
          <p className="py-2 text-gray-600">
            Forgot your password?{" "}
            <Link to="/forgot-password" className="text-blue-400">
              Click Here
            </Link>
          </p>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  // console.log("state = ", state);
  return {
    isLoading: state.user.isLoading,
    loginError: state.user.error ? state.user.error.message : null,
  };
};

const mapDispatchToProps = {
  login,
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
